<template>
  <div class="collegefinder">
    <v-card class="mx-auto my-12 elevation-2" max-width="800px">
      <v-toolbar color="indigo lighten-1" dark dense flat>
        <v-toolbar-title class="text-body-4">
          College Finder based on JEE Main 2022 rank
        </v-toolbar-title>
      </v-toolbar>
      <v-card-text>
        <v-form ref="form" v-model="valid" lazy-validation>
          <br />
          <v-row class="text-center">
            <v-col class="d-flex col-padding" cols="12" sm="4">
              <v-select
                v-model="stateS"
                :items="stateList"
                label="State of Eligibility"
                dense
                :rules="selectRules"
                required
                outlined
              ></v-select>
            </v-col>
            <v-col class="d-flex col-padding" cols="12" sm="4">
              <v-select
                v-model="instituteS"
                :items="instituteType"
                label="Type of Institute"
                dense
                :rules="selectRules"
                required
                outlined
                @change="changeQuotaType()"
              ></v-select>
            </v-col>
            <!--v-col class="d-flex col-padding" cols="12" sm="4">
              <v-select
                v-model="roundS"
                :items="josaaRoundsList"
                label="Round"
                dense
                :rules="selectRules"
                required
                outlined
              ></v-select>
            </v-col -->
            <v-col class="d-flex col-padding" cols="12" sm="4">
              <v-select
                v-model="genderS"
                :items="genderList"
                label="Gender"
                dense
                :rules="selectRules"
                required
                outlined
              ></v-select>
            </v-col>
          </v-row>
          <v-row class="text-center">
            <v-col class="d-flex col-padding" cols="12" sm="4">
              <v-select
                v-model="categoryS"
                :items="categoryList"
                label="Seat Type/Category"
                dense
                :rules="selectRules"
                required
                outlined
                @change="clearCatRank()"
              ></v-select>
            </v-col>
            <v-col class="d-flex col-padding" cols="12" sm="4">
              <v-text-field
                type="number"
                v-model="rank"
                :rules="rankRules"
                label="AIR/CRL"
                required
                outlined
                dense
              ></v-text-field>
            </v-col>
            <v-col class="d-flex col-padding" cols="12" sm="4">
              <v-text-field
                :disabled="isCategoryRank"
                type="number"
                v-model="cat_rank"
                :rules="isCategoryRank ? [] : rankRules"
                label="Category Rank"
                outlined
                dense
              ></v-text-field>
            </v-col>
          </v-row>
          <v-row class="text-center">
            <v-col class="d-flex col-padding" cols="12" sm="4">
              <v-select
                :disabled="isInstituteType"
                v-model="quotaS"
                :items="quotaType"
                label="Type of Quota"
                dense
                :rules="selectRules"
                required
                outlined
              ></v-select>
            </v-col>
            <v-col class="d-flex col-padding" cols="12" sm="4">
              <v-select
                v-model="programS"
                :items="programList"
                label="Program"
                dense
                :rules="selectRules"
                required
                outlined
              ></v-select>
            </v-col>
          </v-row>
          <v-row class="text-center">
            <v-col cols="12" sm="6" offset-sm="3">
              <v-btn
                :disabled="!valid"
                color="indigo darken-2 white--text"
                class="mr-4"
                @click="submit"
              >
                Submit
              </v-btn>

              <v-btn
                outlined
                color="indigo darken-2"
                class="mr-4"
                @click="reset"
              >
                Reset
              </v-btn>
            </v-col>
          </v-row>
        </v-form>
        <div>
          <v-btn text color="black"> Seat Type : {{ this.quotaS }} </v-btn>
        </div>
        <v-data-table
          :headers="headers"
          :items="clgHH"
          item-key="state"
          :items-per-page="5"
          class="elevation-0 Black--text tableClg"
        >
        </v-data-table>
        <div class="text-right"><BranchList /></div
      ></v-card-text>
    </v-card>
  </div>
</template>
<script>
// @ is an alias to /src
/*import clg_hs_round1 from "../json/hs/nit/hs.js";
import clg_hs_round2 from "../json/hs/nit/hs.js";
import clg_hs_round3 from "../json/hs/nit/hs.js";
import clg_hs_round4 from "../json/hs/nit/hs.js";
import clg_hs_round5 from "../json/hs/nit/hs.js";*/
import clg_hs_round6 from "../json/hs/nit/hs.js";
import clg_hs_gfti_round6 from "../json/hs/gfti/hs_gfti.js";

/*import clg_os_round1 from "../json/os/nit/os.js";
import clg_os_round2 from "../json/os/nit/os.js";
import clg_os_round3 from "../json/os/nit/os.js";
import clg_os_round4 from "../json/os/nit/os.js";
import clg_os_round5 from "../json/os/nit/os.js";*/
import clg_os_round6 from "../json/os/nit/os.js";
import clg_os_gfti_round6 from "../json/os/gfti/os_gfti.js";
import clg_ai_iiit_round6 from "../json/os/iiit/ai_iiit.js";

import BranchList from "../components/BranchList";

export default {
  name: "CollegeFinder",
  components: {
    BranchList,
  },
  data: () => ({
    valid: true,
    rank: null,
    cat_rank: null,
    finalClgList: [],
    clgList: [],
    clgHH: [],
    stateS: null,
    homeState: null,
    categoryS: null,
    instituteS: null,
    quotaS: null,
    quota: null,
    genderS: null, //genderS short of gender_Select
    genderList: ["Male", "Female"],
    programS: null,
    course_program: null,
    programList: ["BArch", "BTech/BE/MTech/MSc"],
    instituteType: ["NIT", "IIIT", "GFTI"],
    roundS: 6,
    josaaRoundsList: [1, 2, 3, 4, 5, 6],
    categoryList: [
      "EWS",
      "EWS (PWD)",
      "OBC-NCL",
      "OBC-NCL (PWD)",
      "OPEN",
      "OPEN (PWD)",
      "SC",
      "SC (PWD)",
      "ST",
      "ST (PWD)",
    ],
    stateList: [
      "Andhra Pradesh",
      "Arunachal Pradesh",
      "Assam",
      "Bihar",
      "Chhattisgarh",
      "Goa",
      "Gujarat",
      "Haryana",
      "Himachal Pradesh",
      "Jharkhand",
      "Kerala",
      "Karnataka",
      "Madhya Pradesh",
      "Maharastra",
      "Manipur",
      "Meghalaya",
      "Mizoram",
      "Nagaland",
      "Odisha",
      "Punjab",
      "Rajasthan",
      "Sikkim",
      "Tamilnadu",
      "Telangana",
      "Tripura",
      "Uttarakhand",
      "Uttar Pradesh",
      "West Bengal",
      "Andaman and Nicobar Islands",
      "Chandigarh",
      "Dadra and Nagar Haveli and Daman and Diu",
      "Delhi",
      "Jammu and Kashmir",
      "Ladakh",
      "Lakshadweep",
      "Puducherry",
    ],
    quotaType: ["Home State", "Other State/All India"],
    headers: [
      {
        text: "State | Seat Type | Gender",
        sortable: false,
        value: "state",
        width: "40%",
        class: "indigo lighten-2 white--text",
      },
      {
        text: "Branch",
        value: "branch",
        width: "60%",
        sortable: false,
        class: "indigo lighten-2 white--text",
      },
    ],
    rankRules: [
      (v) => !!v || "Rank is required.",
      (v) =>
        (v && v.length <= 7 && v > 0) ||
        "Rank must be 7 digit positive number.",
    ],
    selectRules: [(v) => !!v || "Field is required."],
  }),
  computed: {
    isCategoryRank() {
      return this.categoryS === "OPEN" ? true : false;
    },
    isInstituteType() {
      return this.instituteS === "IIIT" ? true : false;
    },
  },
  methods: {
    clearCatRank() {
      if (this.categoryS === "OPEN") {
        this.cat_rank = null;
      }
    },
    changeQuotaType(){
       if(this.instituteS ==="IIIT"){
        this.quotaS  = "Other State/All India";
       }
    },
    getClg() {
      this.finalClgList = [];
      this.clgHH = [];
      this.clgList = [];

      //if (this.quotaS === "Home State") {
      switch (this.instituteS) {
        case "NIT":
          this.clgList =
            this.quotaS === "Home State" ? clg_hs_round6 : clg_os_round6;
          break;
        case "GFTI":
          this.clgList =
            this.quotaS === "Home State"
              ? clg_hs_gfti_round6
              : clg_os_gfti_round6;
          break;
        case "IIIT":
          this.clgList = clg_ai_iiit_round6;
          break;
      }
      /* switch (this.roundS) {
        case 1:
          this.clgList =
            this.quotaS === "Home State" ? clg_hs_round1 : clg_os_round1;
          break;
        case 2:
          this.clgList =
            this.quotaS === "Home State" ? clg_hs_round2 : clg_os_round2;
          break;
        case 3:
          this.clgList =
            this.quotaS === "Home State" ? clg_hs_round3 : clg_os_round3;
          break;
        case 4:
          this.clgList =
            this.quotaS === "Home State" ? clg_hs_round4 : clg_os_round4;
          break;
        case 5:
          this.clgList =
            this.quotaS === "Home State" ? clg_hs_round5 : clg_os_round5;
          break;
        default:
          this.clgList =
            this.quotaS === "Home State" ? clg_hs_round6 : clg_os_round6;
          break;
      } */
      if (this.categoryS === "OPEN") {
        this.filterClg(this.clgList, []);
      } else if (this.categoryS === "OPEN (PWD)") {
        this.filterClg(this.clgList, ["OPEN (PWD)"]);
      } else if (this.categoryS === "EWS") {
        this.filterClg(this.clgList, ["EWS"]);
      } else if (this.categoryS === "EWS (PWD)") {
        this.filterClg(this.clgList, ["OPEN (PWD)", "EWS", "EWS (PWD)"]);
      } else if (this.categoryS === "OBC-NCL") {
        this.filterClg(this.clgList, ["OBC-NCL"]);
      } else if (this.categoryS === "OBC-NCL (PWD)") {
        this.filterClg(this.clgList, [
          "OPEN (PWD)",
          "OBC-NCL",
          "OBC-NCL (PWD)",
        ]);
      } else if (this.categoryS === "SC") {
        this.filterClg(this.clgList, ["SC"]);
      } else if (this.categoryS === "SC (PWD)") {
        this.filterClg(this.clgList, ["OPEN (PWD)", "SC", "SC (PWD)"]);
      } else if (this.categoryS === "ST") {
        this.filterClg(this.clgList, ["ST"]);
      } else {
        this.filterClg(this.clgList, ["OPEN (PWD)", "ST", "ST (PWD)"]);
      }
      /* } else {
        if (this.categoryS === "OPEN") {
          this.filterClg(clg_os_round6, [""]);
        } else if (this.categoryS === "OPEN (PWD)") {
          this.filterClg(clg_os_round6, ["OPEN (PWD)"]);
        } else if (this.categoryS === "EWS") {
          this.filterClg(clg_os_round6, ["EWS"]);
        } else if (this.categoryS === "EWS (PWD)") {
          this.filterClg(clg_os_round6, ["OPEN (PWD)", "EWS", "EWS (PWD)"]);
        } else if (this.categoryS === "OBC-NCL") {
          this.filterClg(clg_os_round6, ["OBC-NCL"]);
        } else if (this.categoryS === "OBC-NCL (PWD)") {
          this.filterClg(clg_os_round6, ["OPEN (PWD)", "OBC-NCL", "OBC-NCL (PWD)"]);
        } else if (this.categoryS === "SC") {
          this.filterClg(clg_os_round6, ["SC"]);
        } else if (this.categoryS === "SC (PWD)") {
          this.filterClg(clg_os_round6, ["OPEN (PWD)", "SC", "SC (PWD)"]);
        } else if (this.categoryS === "ST") {
          this.filterClg(clg_os_round6, ["ST"]);
        } else {
          this.filterClg(clg_os_round6, ["OPEN (PWD)", "ST", "ST (PWD)"]);
        }
      } */
    },
    getfinalClgList(state, branchList, seat_type) {
      if (branchList !== null && branchList.length > 0) {
        this.finalClgList.push({
          state: state.state,
          college: state.college,
          seat_type: seat_type,
          branches: branchList
            .filter((c, index) => {
              return branchList.indexOf(c) === index;
            })
            .toString(),
        });
      }
    },
    submit() {
      let validity = this.$refs.form.validate();
      if (validity) {
        this.getHomeState();
        this.getClg();
      }
    },
    reset() {
      this.$refs.form.reset();
      this.$refs.form.resetValidation();
      this.finalClgList = [];
    },
    getHomeState() {
      // setting homestate as per JOSAA rule
      if (this.quotaS === "Home State") {
        if (this.instituteS === "IIIT") {
        this.quota = "NA";
      }else if (this.stateS === "Ladakh") {
          this.quota = "LA";
        } else if (
          this.stateS === "Andaman and Nicobar Islands" ||
          this.stateS === "Lakshadweep"
        ) {
          this.quota = "GO";
        } else if (this.stateS === "Jammu and Kashmir") {
          this.quota = "JK";
        } else {
          this.quota = "HS";
        }
      } 
      /*else {
        this.quota = this.instituteS === "IIIT" ? "AI" : "OS";
      }*/
      if((this.instituteS === "GFTI") && (this.stateS ==="Arunachal Pradesh" || this.stateS ==="Assam" || this.stateS ==="Nagaland" || this.stateS ==="Manipur" || this.stateS ==="Meghalaya" || this.stateS ==="Mizoram" || this.stateS ==="Sikkim" || this.stateS ==="Tripura")){
        this.homeState = "West Bengal"
      } else if (this.stateS === "Andaman and Nicobar Islands") {
        this.homeState = "Puducherry";
      } else if (this.stateS === "Chandigarh") {
        this.homeState = "Delhi";
      } else if (
        this.stateS === "Dadra and Nagar Haveli and Daman and Diu" ||
        this.stateS === "Lakshadweep"
      ) {
        this.homeState = "Goa";
      } else if (this.stateS === "Ladakh") {
        this.homeState = "Jammu and Kashmir";
      } else {
        this.homeState = this.stateS;
      }
    },
    filterClg(clgLists, seat_type) {
      let clgListArray = [],
        clgListArrayC = [];
      let rank = this.rank,
        cat_rank = this.cat_rank;
      let clggroup = [];
      //branchNeutral for Neutral gender, branchFemale for Female and branches for both genders
      this.course_program = this.programS === "BArch" ? "BArch" : "BTech";
      if (this.quotaS === "Home State") {
        // for OPEN seat
        clgListArray = clgLists.filter((clg) => {
          return (
            // GFTI Assam University Home state is Assam and GKCIET, Malda Home state for Assam candidates is West Bengal
            clg.state === ((this.stateS === "Assam" && clg.college ==="Assam University, Silchar")? this.stateS : this.homeState) &&
            clg.seat === "OPEN" &&
            clg.closing_rank >= rank &&
            clg.quota === this.quota &&
            clg.course_program == this.course_program
          );
        });
        //For Category seat
        clgListArrayC = clgLists.filter((clg) => {
          return (
            clg.state === ((this.stateS === "Assam" && clg.college ==="Assam University, Silchar")? this.stateS : this.homeState) &&
            seat_type.indexOf(clg.seat) >= 0 &&
            clg.closing_rank >= cat_rank &&
            clg.quota === this.quota &&
            clg.course_program == this.course_program
          );
        });
      } else {
        //for OPEN seat
        clgListArray = clgLists.filter((clg) => {
          return (
            clg.state !== ((this.stateS === "Assam" && clg.college ==="Assam University, Silchar")? this.stateS : this.homeState) &&
            clg.seat === "OPEN" &&
            clg.closing_rank >= rank &&
            (clg.quota === "AI" || clg.quota === "OS") &&
            clg.course_program == this.course_program
          );
        });
        // for Category seat
        clgListArrayC = clgLists.filter((clg) => {
          return (
            clg.state !== ((this.stateS === "Assam" && clg.college ==="Assam University, Silchar")? this.stateS : this.homeState) &&
            seat_type.indexOf(clg.seat) >= 0 &&
            clg.closing_rank >= cat_rank &&
            (clg.quota === "AI" || clg.quota === "OS") &&
            clg.course_program == this.course_program
          );
        });
      }
      if (this.genderS === "Male") {
        this.finalClgList = clgListArray
          .concat(clgListArrayC)
          .filter((clg) => clg.gender === "Neutral");
      } else {
        this.finalClgList = clgListArray.concat(clgListArrayC);
      }
      clggroup = this.finalClgList.reduce((r, a) => {
        r["'" + a.college + " | " + a.seat + " | " + a.gender + "'"] = [
          ...(r["'" + a.college + " | " + a.seat + " | " + a.gender + "'"] ||
            []),
          a.branch,
        ];
        return r;
      }, {});

      Object.entries(clggroup).forEach(([key, value]) => {
        this.clgHH.push({
          state: key.toString(),
          branch: value.toString().split(",").join(", "),
        });
      });
      this.clgHH.sort((a, b) => (a.state > b.state ? 1 : -1));
    },
  },
};
</script>
<style scoped>
.col-padding {
  padding-top: 0px !important;
  padding-bottom: 0px !important;
}
</style>
